*{margin:0;padding:0}body{width:100%;min-height:100vh;display:flex;justify-content:center;align-items:center;background:#93cede;background:-webkit-gradient(left top, right top, color-stop(0%, #93cede), color-stop(41%, #75bdd1), color-stop(100%, #0084a8));background:-webkit-gradient(linear, left top, right top, from(#93cede), color-stop(41%, #75bdd1), to(#0084a8));background:linear-gradient(to right, #93cede 0%, #75bdd1 41%, #0084a8 100%);filter:progid:DXImageTransform.Microsoft.gradient( startColorstr='#93cede', endColorstr='#0084a8', GradientType=1 )}

.app{padding:50px;box-sizing:border-box;background:rgba(214,214,214,0.24);border-radius:45px;box-shadow:7px 11px 56px -20px #fff}.app p{color:#f3f4fa}

.clock-wrapper{font-family:'Montserrat', 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;font-size:5rem;width:310px}

.dateInfo-wrapper{text-align:center;font-family:'Montserrat', 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;font-size:2rem}

